.header {
  background-color: #030303;
}
.header__wrapper {
  display: flex;
  align-items: center;
  padding: 20px 0;
}
.header__content {
  display: flex;
  flex-grow: 1;
  gap: 56px;
  justify-content: center;
  font-weight: 500;
  font-size: 32px;
  line-height: 1.2;
  letter-spacing: -2%;
  text-transform: uppercase;
}
.header__content h1 {
  font-size: 32px;
}
.header__arrow {
  height: 40px;
  aspect-ratio: 1;
}
.header__arrow--l {
  transform: rotate(180deg);
}

@media only screen and (max-width: 1023px) {
  .header__content h1 {
    font-size: 26px;
  }
  .header__arrow {
    height: 30px;
  }
}
@media only screen and (max-width: 767px) {
  .header__content h1 {
    font-size: 14px;
  }
  .header__arrow {
    height: 16px;
  }
}