.hero {
  height: 1176px;
  position: relative;
  background-color: gray;
  overflow: hidden;
  background: linear-gradient(to bottom, #0f0f0f 0%, #373737 100%);
}
.hero__figure {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 120%;
  object-fit: cover;
}
.hero__logo {
  position: absolute;
  top: 0;
  right: 0;
  width: 416px;
  height: 121px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #030303;
}
.hero__banner-data {
  font-size: 14px;
  display: flex;
  padding: 0 10px;
  width: 100%;
  justify-content: space-between;
}
.hero__banner {
  font-size: 14px;
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  gap: 20px;
  padding: 26px;
  align-items: center;
  flex-direction: column;
  width: 220px;
  background-color: #030303;
}
.hero__banner::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 120px;
  width: 15px;
  background-color: #01d344;
}
.hero__decor {
  position: absolute;
  inset: 0;
  pointer-events: none;
}
.hero__text-decor {
  font-size: 24px;
  font-weight: 300;
  position: absolute;
}
.hero__text-decor--lg {
  font-size: 60px;
}
.hero__text-decor--1 {
  top: 10%;
  left: 30%;
}
.hero__text-decor--2 {
  top: 20%;
  left: 67%;
}
.hero__text-decor--3 {
  top: 50%;
  left: 30%;
}
.hero__text-decor--4 {
  top: 80%;
  left: 5%;
}
.hero__text-decor--5 {
  top: 8%;
  left: 66%;
}
.hero__text-decor--6 {
  top: 68%;
  left: 66%;
}
.hero__text-decor--7 {
  top: 8%;
  left: 66%;
}
.hero__wrapper {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.hero__features {
  position: absolute;
  top: 32px;
  left: 260px;
  display: flex;
  align-items: center;
  gap: 76px;
  font-size: 12px;
}
.hero__features-item {
  display: flex;
  align-items: center;
  gap: 16px;
}
.hero__footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  z-index: 1;
  margin-top: auto;
  font-weight: 500;
  font-size: 24px;
  padding: 6px 0;
  text-align: center;
  color: #030303;
  background-color: #01d344;
}
.hero__timer {
  width: 416px;
  position: absolute;
  right: 0;
  bottom: 34px;
  background-color: #fff;
  color: #030303;
}
.hero__timer span {
  display: inline-block;
}
.hero__timer-header {
  font-size: 12px;
  display: flex;
  justify-content: space-between;
  padding: 16px 32px;
  color: #fff;
  background-color: #030303;
}
.hero__timer-content {
  justify-content: center;
  display: flex;
  text-align: center;
  font-size: 96px;
}

@media only screen and (max-width: 1199px) {
  .hero__logo, .hero__features {
    display: none;
  }
  .hero__text-decor {
    white-space: nowrap;
  }
  .hero__text-decor--lg {
    font-size: 50px;
  }
}
@media only screen and (max-width: 767px) {
  .hero {
    height: 800px;
  }
  .hero__text-decor {
    display: none;
  }
  .hero__timer {
    width: 100%;
  }
  .hero__timer-content {
    font-size: 78px;
  }
  .hero__banner {
    padding: 16px;
    width: auto;
  }
  .hero__banner::before {
    height: 80px;
    width: 10px;
  }
  .hero__banner-data {
    padding: 0;
    font-size: 12px;
  }
  .hero__banner-text {
    width: 98px;
    height: 275px;
  }
  .hero__banner-img {
    width: 98px;
  }
  .hero__footer {
    font-size: 20px;
  }
}