.footer {
  position: relative;
}
.footer .footer__bg {
  position: absolute;
  width: 100%;
  height: 100%;
  inset: 0;
  object-fit: cover;
}
.footer span {
  font-size: 24px;
}
.footer__wrapper {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  padding: 20px 0;
  font-weight: 600;
  font-size: 24px;
  justify-content: space-between;
  text-transform: uppercase;
}

@media only screen and (max-width: 767px) {
  .footer span {
    font-size: 16px;
  }
  .footer__wrapper {
    font-size: 18px;
    gap: 16px;
  }
  .footer__year {
    display: none;
  }
}