.run {
  width: 100vw;
  overflow: hidden;
}
.run__mobile {
  height: 220px;
  flex-grow: 1;
  position: relative;
  display: none;
}
.run__mobile video {
  width: 100%;
  height: 120%;
  inset: 0;
  object-fit: cover;
}
.run__marquee {
  display: flex;
  width: 200vw;
  height: 100%;
  animation: marquee 20s linear infinite;
}
.run__item {
  background-color: #fff;
  display: flex;
  height: 381px;
  flex-shrink: 0;
}
.run__img-box {
  flex-grow: 1;
  margin-left: -2px;
  height: 100%;
  position: relative;
  background-color: #fff;
}
.run__img-box img {
  height: 100%;
}
.run__video-box {
  flex-grow: 1;
  height: 100%;
  position: relative;
}
.run__video-box video {
  width: 100%;
  height: 120%;
  inset: 0;
  object-fit: cover;
}

@media only screen and (max-width: 1023px) {
  .run {
    height: auto;
  }
  .run__mobile {
    display: block;
  }
  .run__item {
    flex-direction: column;
  }
  .run__video-box {
    display: none;
  }
}
@keyframes marquee {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100vw);
  }
}