.modules {
  padding: 40px 0;
}
.modules__list {
  display: flex;
  justify-content: space-between;
}
.modules__item {
  display: flex;
  width: 30%;
  flex-direction: column;
  gap: 10px;
}
.modules__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}
.modules__header h2 {
  font-size: 20px;
  font-weight: 500;
}
.modules__header span {
  font-size: 40px;
  font-weight: 300;
}
.modules__wrapper {
  font-size: 12px;
  font-weight: 300;
  display: flex;
  justify-content: space-between;
}
.modules__wrapper--bordered {
  border-bottom: 1px solid #fff;
  margin-bottom: 8px;
  padding-bottom: 8px;
}
.modules__value {
  font-weight: 500;
}
.modules__text {
  font-size: 12px;
  font-weight: 300;
}
.modules__rating {
  display: flex;
  gap: 2px;
}
.modules__rating i {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: #fff;
  border: 1px solid #fff;
}
.modules__rating i.empty {
  background-color: #030303;
}

@media only screen and (max-width: 1023px) {
  .modules__list {
    gap: 40px;
    flex-direction: column;
  }
  .modules__item {
    width: 100%;
  }
}
@media only screen and (max-width: 767px) {
  .modules__header h2 {
    font-size: 16px;
  }
  .modules__header span {
    font-size: 32px;
  }
}