@charset "UTF-8";
.notice {
  color: #000;
  background-color: #d9d9d9;
}
.notice__content {
  background-color: #fff;
}
.notice__caption {
  font-weight: 500;
  font-size: 32px;
  margin-bottom: 32px;
}
.notice__subcation {
  margin-bottom: 12px;
  font-size: 12px;
  font-weight: 300;
  display: flex;
  justify-content: space-between;
  padding-bottom: 12px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.4666666667);
}
.notice__wrapper {
  display: flex;
  width: 100%;
}
.notice__content-wrapper {
  max-width: 780px;
  display: flex;
  gap: 100px;
  padding: 32px 32px 24px 32px;
}
.notice__decor-content {
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.notice__footer {
  margin-top: 6px;
  color: #000;
  font-size: 32px;
  font-weight: 500;
  padding: 20px 32px;
  display: flex;
  width: 100%;
  justify-content: space-between;
  background-color: #01d344;
}
.notice__decor {
  flex-grow: 1;
  position: relative;
  overflow: hidden;
}
.notice__decor img {
  position: absolute;
  width: 100%;
  height: 100%;
  inset: 0;
  object-fit: cover;
  transition: transform 4s ease-in-out;
  transform-origin: center;
  animation: stretchMove 4s infinite alternate ease-in-out;
}

@media only screen and (max-width: 1023px) {
  .notice__footer, .notice__caption {
    font-size: 24px;
  }
  .notice__wrapper {
    flex-direction: column-reverse;
  }
  .notice__content-wrapper {
    max-width: 100%;
  }
}
@media only screen and (max-width: 767px) {
  .notice__footer {
    font-size: 16px;
  }
  .notice__caption {
    font-size: 2-px;
  }
  .notice__decor-content {
    gap: 20px;
    width: 50%;
  }
  .notice__content-wrapper {
    max-width: 100%;
    gap: 20px;
    padding: 20px;
    flex-direction: column;
  }
}
@keyframes stretchMove {
  0% {
    transform: scale(1, 1) translateX(0);
  }
  50% {
    transform: scale(1.2, 1.3) translateX(10px); /* смещение на 10px вправо */
  }
  100% {
    transform: scale(1, 1) translateX(0);
  }
}